.navigationlist{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center
  }
  
  .fit_picture{
    background-color: rgb(255, 255, 255);
  }
  
  .textNa{
    color: rgba(19, 21, 21, 0.844);
    font-size: 18px;
    font-weight: 700;
  }
  
  .navigationlist_fixed{
    display: flex;
    flex-direction: column;
  }
  .li_fixed{
    margin-bottom: 15px;
    margin-left: 9px;
  }
  .navLi_fixed{
  
    text-decoration: none;
    list-style: none;
    color: #000000;
      font-size: 14px;
      font-weight: 500;
      text-transform: uppercase;
  }
  
  .navigation_link_active{
  padding: 10px 0;
  border-radius: 10%;
    background-color: rgba(0, 195, 255, 0.296);
  }
  
  .navLi{
    // padding: 15px;
    text-decoration: none;
    list-style: none;
    color: #000000;
      font-size: 18px;
      font-weight: 500;
      text-transform: uppercase;
  }
  
  .logoName{
    
    display: block;
    max-width: 100%;
    height: auto;
    box-sizing: border-box;
  }
  
  .navigationlist__btn{
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  