.Overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 12340;
  overflow-y: auto;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  transition: opacity 0.15s linear;
}



.Modal {
  background-color: white;
  position: relative;
  width: 250px;
  height: auto;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1200;
}

.button_svg {
  position: absolute;
  right: 0;
  top: 0;
  color: #999;
  transition: 0.1s ease-in-out;
  transition-property: color, opacity;
  margin: 0;
  border: none;
  border-radius: 0;
  overflow: visible;
  font: inherit;
  color: inherit;
  text-transform: none;
  padding: 0;
  background-color: transparent;
  display: inline-block;
  fill: currentcolor;
  line-height: 0;
  padding: 10px;
}

.text_title_midl {
  text-align: center;
  color: #535258;
  font-size: 30px;
  font-weight: 400;
  margin: 0;
  text-transform: uppercase;
}

p {
  color: #535258;
  padding-top: 20px;
  font-size: 15px;
  font-weight: 300;
  margin: 0;
  text-transform: uppercase;
}



/* form */


.contactForm{
  height: auto;
  font-size: 24px;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
text-align: center;
  border-radius: 5px;
  border: thick double #32a1ce;
}
.contactFormItem{
  border: solid;
  margin-bottom: 10px;
}



input{
  font-size: 20px;
  height: 40px;
}

.contactBtn{
  background-color: #4267B2;
  padding: 8px 16px;
  border-radius: 3px;
  transition: all 200ms ease;
  font-weight: 500;
  text-align: center;
  display: inline-block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 16px;
  line-height: 24px;
  font-style: normal;
  min-width: 180px;
}

